.card-container{
    display: flex;
    flex-direction: column;
    background-color: rgb(80, 24, 24);
    border-radius: 5px;
    border: 1px solid gray;
    padding: 25px;
    transform: translateZ(0);
}
.card-container h1, .card-container p{
    color: white;
    font-size: 15px;
}

.card-container:hover{
    transform: scale(1.05);
    transition: all ease-in .3s;
}
